export default {
  name: 'ticketRemark',
  props: {
    remark: Object
  },
  data () {
    return {
      specialList: ['适用人群:', '退款规则:']
    }
  },
  computed: {
    value () {
      if (this.remark) {
        for (let key in this.remark) {
          if (key === 'title') {
            continue
          }
          return this.remark[key]
        }
      } else {
        return '暂无'
      }
    }
  }
}
